// This cannot assume using a bunch of other resources we normally use. Thus, it's merely a namespaced object.

// Ideally, this was already created in a template with config and placements.
// This is just a precaution so we don't throw when we check.
var runSlots = runSlots || {};

var googletag = googletag || {};
googletag.cmd = googletag.cmd || [];

(function() {
    // disable if no config or partners
    return typeof runSlots.config === 'object' && typeof runSlots.config.partners === 'object';
})() && (function() {
    var startupEpoch = new Date().getTime();
    var lastRotatedEpoch; // filled after first ad shown ans start measured
    var finishEpoch = 0;

    // our in-page ad slots; short for "display slots"
    var activatedSlots = {};

    // logging/debug stuff
    var _logLogPrefix = '[RUNSLOTS]';
    var _logLogable = typeof console === 'object' && typeof console.log === 'function';
    var _pbDebugging = (/(\?|\&)pbjs_debug\=(true|1|on)(\&|$)/).test(document.location.search);
    // allow for debugging via cookie for "always on"
    if(!_pbDebugging) {
        _pbDebugging = (/(^|\s+)runSlots_debug=(true|1|on)[;:]/i).test(document.cookie);
    }

    var _ads_blocked = typeof window.ADBLOCKED === 'undefined' || window.ADBLOCKED !== false;
    var _debugBidderGetParm = 'pbjs_bidders';
    var _debugBidderRegex = new RegExp(_debugBidderGetParm+'\\=([^&]+)?');
    var _jafoAvailable = false;
    var _jafoCheckInterval = 150;
    var _jafoChecking;
    var _jafoWaiting = [];
    var _partnerAttribute = 'data-partner';
    var _partnersLoaded = [];
    var _partnersReady = false;
    var _partnersWaiting = [];
    var _rotateDebounceWait = 1500;
    var _retargetAmazon = false;
    var isInitialized = false;

    var loggedIn = Imgur.Environment.auth.isLoggedIn;
    var _floor = 0.10;

    // analytic pixels for sumo integration
    var sumoAdReportingPixel = Imgur.Util.getAnalyticsCdnUrl() + '/reporting.gif?';

    // ad rotation
    var viewedSlots = [];
    runSlots.adRefresh = true;
    var refreshCount = 0;
    var adRefreshDelay = 15;

    // datadog reporting
    var _socket;
    var _shouldReport = Math.random() < 0.05; // only 5% of clients should report ads -- for initial testing

    runSlots.currentAdInfo = {};

    // we gotta wait for the rest to load
    function _check_jafo() {
        if (typeof Imgur !== 'function' || typeof Imgur.Util !== 'object' || typeof Imgur.Util.jafoLog !== 'function') {
            return;
        }

        var qItem;
        _jafoAvailable = true;
        clearInterval(_jafoChecking);
        while(_jafoWaiting.length > 0) {
            runSlots.logImpression.apply(this, _jafoWaiting.shift())
        }
    }

    // start checking for JAFO right away
    _jafoChecking = setInterval(_check_jafo, _jafoCheckInterval);

    // general wrapper for console logging
    function _console(method, args) {
        if(!_pbDebugging || !_logLogable || typeof method !== 'string' || typeof console[method] !== 'function') {
            // bail quick
            return;
        }

        // add our prefix to our arg list
        var argsNew = [].slice.call(args);
        argsNew.unshift(_logLogPrefix);

        console[method].apply(console, argsNew);
    }

    function _log() {
        return _console('log', arguments);
    }

    function _info() {
        return _console('info', arguments);
    }

    function _warn() {
        return _console('warn', arguments);
    }

    function itemShowsAd(item) {
        return item && item.adConfig && item.adConfig.showsAds;
    }

    runSlots.apply_targeting_to_active_slots = function(key, val) {
        var iter;

        if(typeof key === 'string' && key.length > 0) {
            if(val) {
                googletag.cmd.push(function() {
                    googletag.pubads().setTargeting(key, val);
                    _info('Set targeting for "'+key+'"', val);
                });
            } else {
                googletag.cmd.push(function() {
                    // clear the targetting for empty/falsy values
                    googletag.pubads().clearTargeting(key);
                    _info('Cleared targeting for "'+key+'"');
                });
            }
        }
    };

    runSlots.createSlotHtml = function() {
        runSlots.config.active_slots.forEach(function(slot) {
            var element = document.getElementById(slot.element);

            if(!element) {
                element = document.createElement('div');
                element.setAttribute('id', slot.element);

                var $selector = $(slot.insertSelector);
                $selector[slot.insertMethod].call($selector, element);
            }

            $(element).addClass('advertisement div-gpt-ad-active-'+slot.name);
            _log('Creating element #'+slot.element+' in the DOM for slot '+slot.name+'.');
        });
    };

    runSlots.defineSlots = function() {
        googletag.cmd.push(function() {
            runSlots.config.active_slots.forEach(function(slot) {
                activatedSlots[slot.name] = googletag.defineSlot(slot.slot_id,  slot.sizes, slot.element);
                activatedSlots[slot.name].addService(googletag.pubads());
                activatedSlots[slot.name].i_name = slot.name;
            });

            runSlots.apply_targeting_to_active_slots('adblock', window.usesABP ? 'true' : 'false');
            runSlots.apply_targeting_to_active_slots('login', loggedIn);
            runSlots.apply_targeting_to_active_slots('SSL', (location.protocol === 'https:') + '');

        });
    };

    // alias for googletag.display() wrapping
    runSlots.display = function(ele) {
        googletag.cmd.push(function() {
            googletag.pubads().collapseEmptyDivs();
            googletag.display(ele);

            if(!finishEpoch) {
                finishEpoch = new Date().getTime();
                var delta = parseInt(finishEpoch - startupEpoch, 10);
                _info('Displayed first impressions in '+(delta/1000)+' seconds');
            }
        });
    };

    runSlots.display_slots = function(ele) {
        googletag.cmd.push(function() {
            runSlots.setBidderTargeting();

            googletag.pubads().enableSingleRequest();
            googletag.pubads().set('page_url', window.location.href);
            googletag.pubads().addEventListener('slotRenderEnded', runSlots.handleSlotRender);
            googletag.pubads().addEventListener('impressionViewable', runSlots.slotBecameViewable);
            googletag.enableServices();

            for(var i in activatedSlots) {
                if(activatedSlots[i]) {
                    runSlots.display(activatedSlots[i].getSlotElementId());
                }
            }

            runSlots.pbjsRequestBids(function() {
                pbjs.setTargetingForGPTAsync();
                runSlots.rotate();
            });
        });
    };

    runSlots.slotBecameViewable = function(data) {
        // data contains:
        // serviceName => Name of the service that rendered the slot.
        // slot => The slot which contains the impression that became viewable.
        viewedSlots.push(data.slot.i_name);
        _shouldReport && _socket.emit('impressionViewable', {slotName: data.slot.i_name});
        _shouldReport && Imgur.Util.firePixel(sumoAdReportingPixel + 'event=impressionViewable&slotName=' + data.slot.i_name);
    };

    runSlots.galleryNav = function(item) {
        try {
            document.getElementById('div-gpt-ad-spotlight').contentWindow.googletag.pubads().refresh();
        } catch(e) {/* noop -- we don't care about any errors inside the spotlight iframe*/}

        _info('Navigation to new item / rotation.', item);

        // assign the new gallery item
        runSlots.item = item;

        var $ads = $('.advertisement:not(#under-sidebar-parent),.advertisement:not(#under-sidebar-parent) .panel-ad');
        if(!itemShowsAd(runSlots.item)) {
            $ads.addClass('nodisplay');
            runSlots.clearAdRefreshTimeout(); // prevent timed refresh of ads
            _log('Nothing to show on navigation (NSFW or PP?)');
            return;
        } else {
            $ads.removeClass('nodisplay');
        }

        runSlots.renderGalleryNav();
    };

    runSlots.renderGalleryNav = _.debounce(function() {
        if (!itemShowsAd(runSlots.item)) {
            return;
        }

        // if landing item was NSFW or defered init for some reason, attempt to init again
        if(!isInitialized) {
            runSlots.init();
            return;
        }

        runSlots.setTargeting();

        runSlots.pbjsRequestBids(function() {
            pbjs.setTargetingForGPTAsync();
            runSlots.rotate();
        });
    }, _rotateDebounceWait, {leading: true});

    runSlots.getBidderList = function(slotConfig) {
        var matchedDebugBidders = document.location.search.match(_debugBidderRegex);
        var participants;
        var bidConfig = [];

        if(!rawUtils.is_obj(slotConfig, true)) {
            _warn('Getting bidders failed for empty slot config.', slotConfig);
            return;
        }

        if(rawUtils.is_array(matchedDebugBidders, 2)) {
            if(typeof matchedDebugBidders[1] === 'string') {
                participants = matchedDebugBidders[1].split(',');
            } else {
                // no desired participants
                return [];
            }
        }

        if(rawUtils.is_array(participants, true)) {
            // selective only
            for(var iter = 0; iter < participants.length; iter++) {
                var bidder = participants[iter];
                if(rawUtils.is_obj(slotConfig[bidder], true) && rawUtils.is_obj(slotConfig[bidder].config, true)) {
                    bidConfig.push(slotConfig[bidder].config);
                    _info('Added debug bidder '+bidder+'.', slotConfig[bidder]);
                } else {
                    _warn('Desired bidder '+bidder+' does not have a valid slot config.', slotConfig);
                }
            }
        } else {
            // only production-ready
            for(var iter in slotConfig) {
                if(rawUtils.is_obj(slotConfig[iter]) && rawUtils.is_obj(slotConfig[iter].config, true)) {
                    if(slotConfig[iter].prod) {
                        bidConfig.push(slotConfig[iter].config);
                        _info('Added prod bidder '+iter+'.', slotConfig[iter]);
                    } else {
                        _info('Bidder '+iter+' is not marked for production.', slotConfig[iter]);
                    }
                } else {
                    _warn('Prod bidder '+iter+' does not have a valid slot config.', slotConfig);
                }
            }
        }

        if(bidConfig.length > 0) {
            return bidConfig;
        }
    };

    runSlots.handlePartner = function(ev) {
        var partner;

        try{
            partner = ev.path[0].getAttribute(_partnerAttribute);
        } catch(e) {
            // just count them then... no idea who likely an old browser
            partner = 'unknown';
        }

        _partnersLoaded.push(partner);
        _log('Partner '+partner+' loaded.');


        if(_partnersWaiting.length === _partnersLoaded.length) {
            _partnersReady = true;
            runSlots.handleAllPartnersLoaded();
            _info('All on-page partners are loaded.', {
                waiting: _partnersWaiting,
                loaded: _partnersLoaded
            });
        }
    };

    runSlots.handleAllPartnersLoaded = function () {
        googletag.cmd.push(function() {
            // because of this, every ad pull should be treated like rotation
            googletag.pubads().disableInitialLoad();

            runSlots.defineSlots();
            runSlots.setTargeting();

            pbjs.que.push(function() {
                runSlots.pbjsApplyAllBidderSettings();
                runSlots.pbjs_add_units();
            });

            runSlots.display_slots();
        });
    };

    runSlots.clearAdRefreshTimeout = function () {
        window.clearTimeout(runSlots._adRefreshTimeout);
        runSlots._adRefreshTimeout = null;
    };

    runSlots.handleSlotRender = function(eventData) {
        if(typeof eventData === 'object') {
            runSlots.logImpression(eventData.slot.getAdUnitPath(), eventData.size, eventData.slot.i_name);

            var bidderInfo;
            //search the response object for prebid info
            for (key in eventData.slot) {
                if (!eventData.slot.hasOwnProperty(key)){ continue; }
                if (!eventData.slot[key] || !eventData.slot[key].hb_bidder) { continue; } // use the hb_bidder key to tell us if we found the prebid object
                bidderInfo = eventData.slot[key];
            }

            if (!bidderInfo.hb_adid[0] || !bidderInfo.hb_bidder[0] || !bidderInfo.hb_pb[0] || !bidderInfo.oxb[0]) { return; }

            // store Ad Info & Query IDs for bad ad reporting
            runSlots.currentAdInfo[eventData.slot.i_name] = {
                adid: bidderInfo.hb_adid[0],
                bidder: bidderInfo.hb_bidder[0],
                cpm: bidderInfo.hb_pb[0],
                oxb: bidderInfo.oxb[0], // Unsure what this is, but just capturing since it can't hurt to have extra data. Seems like an openx ID string, but its present even on prebid items.
                qqid: eventData.slot.getEscapedQemQueryId()
            }
        }
    };

    // Primary init - this should only be run once
    runSlots.init = function() {
        _log('Attempting init.');

        if(!itemShowsAd(runSlots.item)) {
            // defer until after this item
            _info('Deferred init');
            return;
        }

        runSlots.filterIntendedSlots();

        if(_ads_blocked) {
            isInitialized = true;
            if (window.usesABP) {
                runSlots.initSpotlight();
            }
            _warn('Adblocker detected. Aborting runSlots.init().');
            return;
        }

        runSlots.initSpotlight();
        runSlots.loadAllPartners();
        runSlots.createSlotHtml();

        // mark all slots as viewed for initial load
        viewedSlots = runSlots.config.active_slots.map(function(slot) { return slot.name });

        isInitialized = true;

        if (_shouldReport) {
            _socket = io(Imgur.Environment.socketUrl, {
                transports: ['websocket'],
                upgrade: false,
                reconnection: false
            });
        }
    };

    runSlots.filterIntendedSlots = function() {
        runSlots.config.active_slots = runSlots.config.active_slots.filter(function(slot){
            // NO leaderboard for logged in users
            if (slot.name == 'top_banner' && loggedIn){
                return false;
            }

            // Turn on spotlight for all ABP users regardless of login state - it will be the only ad shown
            if (slot.name == 'spotlight' && window.usesABP){
                return true;
            }

            // NO spotlights for anon users
            if (slot.name == 'spotlight' && !loggedIn){
                return false;
            }

            return true;
        });
    };

    runSlots.initSpotlight = function () {
        if (!loggedIn && !window.usesABP) {
            // If the user is anonymous and has NO adblock, just show them programatic ads.
            return;
        }

        // Determine whether to load amazon or criteo for evaluation. We will adjust the inclusion criteria later.
        // For now amazon to 0% and Criteo to 100% of logged out, ABP users
        var queryString = '';
        if (window.usesABP && !loggedIn) {
            var roll = Math.random();
            if (roll < 0.1) { //10% bucket for amazon
                queryString = '?amazon_first=true'
            } else if (roll < 0.55) { // 45% bucket for criteo (55-10 since first 10% is caught above)
                queryString = '?criteo_first=true'
            } // remaining 45% goes straight to DFP
        }

        var iframe = document.createElement('iframe');
        iframe.setAttribute('id', 'div-gpt-ad-spotlight');
        iframe.setAttribute('class', 'advertisement');
        iframe.setAttribute('src', '/spotlight-iframe' + queryString);

        if (!itemShowsAd(runSlots.item)) {
            iframe.setAttribute('class', 'nodisplay');
        }

        $('#inside .post-container').before(iframe);
    };

    runSlots.loadAllPartners = function() {
        clearTimeout(pbjs.partner_load_timeout);

        for(var iter in runSlots.config.partners) {
            if(runSlots.config.partners.hasOwnProperty(iter)) {
                runSlots.loadPartner(iter)
            }
        }
    };

    runSlots.loadPartner = function(partner) {
        var scrSrc = rawUtils.dive(runSlots, 'config.partners.'+partner+'.src');

        if(!scrSrc || typeof scrSrc !== 'string' || scrSrc.length == 0) {
            return;
        }

        _partnersWaiting.push(partner);

        var ele = document.createElement('script');
        var anchor = document.getElementsByTagName('script')[0];

        ele.setAttribute(_partnerAttribute, partner);
        ele.type = 'text/javascript';
        ele.src = scrSrc;

        if(partner === 'gpt') {
            googletag.cmd.push(function() {
                // have to mimic an event
                runSlots.handlePartner({
                    path: [ele]
                });
            });
        } else {
            ele.addEventListener('load', runSlots.handlePartner);
            ele.addEventListener('error', runSlots.handlePartner);
        }

        anchor.parentNode.insertBefore(ele, anchor);
    };

    runSlots.logImpression = function(adSlot, dimensions, internal_name) {
        if(!_jafoAvailable) {
            _jafoWaiting.push([adSlot, dimensions]);
            return;
        }

        if(lastRotatedEpoch) {
            var deltaEpoch = parseFloat(((new Date().getTime() - lastRotatedEpoch) / 1000));
            _info('Logging impression for '+internal_name+' '+deltaEpoch+' seconds after all rotation began.');
        }

        Imgur.Util.jafoLog({
            event: 'adImpression',
            meta: {
                ad_keywords: runSlots.item.adConfig.keywords,
                ad_size: dimensions,
                internal_name: internal_name
            }
        });

        _info('Logged delivered impression', arguments);
    };

    runSlots.pbjs_add_units = function() {
        var pbjsUnits = [];

        runSlots.config.active_slots.forEach(function(slot) {
            if(!slot.prebid) {
                return
            }

            // if slot is not new or hasn't been viewed yet, don't bid on it
            if (viewedSlots.indexOf(slot.name) === -1) {
                return
            }

            var bidders = runSlots.getBidderList(slot.prebid);
            pbjsUnits.push({
                code: slot.element,
                sizes: slot.sizes,
                bids: bidders,
            });
        });

        pbjs.addAdUnits(pbjsUnits);
    };

    runSlots.pbjsApplyAllBidderSettings = function() {
        // apply any custom pricing adjustment functions to the bid process
        for(vendor in runSlots.config.bidder_adjustments) {
            if(runSlots.config.bidder_adjustments.hasOwnProperty(vendor) && !isNaN(runSlots.config.bidder_adjustments[vendor])) {
                runSlots.pbjs_apply_bidderSettings_for(vendor, runSlots.config.bidder_adjustments[vendor]);
            }
        }

        // add in the granularity as "standard" so it applies to all
        if(typeof pbjs.bidderSettings.standard !== 'object') {
            pbjs.bidderSettings.standard = {};
        }

        // Yes, this structure sucks a little - not my choice.
        // In order to change one of these, we have to set them all up.
        // Using an array rather than indexed objects makes it a PITA.
        // For details see the following:
        // http://prebid.org/dev-docs/examples/custom-price-bucket.html
        // http://prebid.org/dev-docs/publisher-api-reference.html#bidResponse
        pbjs.bidderSettings.standard.adserverTargeting = [
            {
                key: "hb_bidder",
                val: function(bidResponse) {
                    // we have to imitate the default... :/
                    return bidResponse.bidder;
                }
            },
            {
                key: "hb_adid",
                // we have to imitate the default... :/
                val: function(bidResponse) {
                    return bidResponse.adId;
                }
            },
            {
                key: "hb_pb",
                val: function(bidResponse) {
                    // Note that hb_pb is what this is called for prebid and the key this
                    // func is bound to. We return the "high granularity" price from the response
                    return bidResponse.pbHg;
                }
            }
        ];
    };

    runSlots.pbjs_apply_bidderSettings_for = function(vendor, adjustment) {
        if(typeof pbjs.bidderSettings !== 'object') {
            pbjs.bidderSettings = {};
        }

        if(typeof pbjs.bidderSettings[vendor] !== 'object') {
            pbjs.bidderSettings[vendor] = {};
        }

        pbjs.bidderSettings[vendor].bidCpmAdjustment = function(bidAmt) {
            var adjusted = bidAmt * adjustment;

            if (bidAmt < _floor) {
                _info('Adjusted '+vendor+' from '+bidAmt+' to 0 (bid was below floor of '+_floor+')');
                return 0;
            }

            _info('Adjusted '+vendor+' bid by '+adjustment+' (from '+bidAmt+' to '+adjusted+')');

            return adjusted;
        };
    };

    runSlots.pbjsRequestBids = function(cb, timeout) {
        pbjs.requestBids({
            timeout: timeout || PREBID_TIMEOUT,
            bidsBackHandler: function(bidResponses) {
                if(typeof cb === 'function') {
                    cb();
                }

                // log returned CPM values in datadog and sumo
                for (var slotElement in bidResponses) {
                    if (!bidResponses.hasOwnProperty(slotElement)) {
                        continue;
                    }

                    var topBid = {};
                    bidResponses[slotElement].bids.forEach(function(bid){
                        if (!topBid || bid.cpm > topBid.cpm) {
                            topBid = bid;
                        }
                    });
                    _shouldReport && _socket.emit('topBid', {
                        slotElement: slotElement,
                        cpm: topBid.cpm,
                        bidder: topBid.bidder
                    });

                    _shouldReport && Imgur.Util.firePixel(sumoAdReportingPixel
                        + 'event=topBid&slotElement=' + slotElement
                        + '&cpm=' + topBid.cpm
                        + '&bidder=' + topBid.bidder);
                }
            }
        });
    };

    runSlots.rotate = function() {
        lastRotatedEpoch = new Date().getTime();
        _info('Rotating units.');

        googletag.cmd.push(function(){
            runSlots.clearAdRefreshTimeout();
            runSlots.setBidderTargeting();

            runSlots.apply_targeting_to_active_slots('refresh_count', refreshCount.toString());
            refreshCount++;

            googletag.pubads().set('page_url', location.href);
            var slotsToRefresh = rawUtils.get_values(activatedSlots);
            if (runSlots._shouldTimedRefresh) {
                slotsToRefresh = runSlots.filterViewedSlots(slotsToRefresh);
                slotsToRefresh = slotsToRefresh.filter(function(slot){ return slot.i_name != 'spotlight' }); // never refresh spotlight slot
                runSlots._shouldTimedRefresh = false;
            }
            googletag.pubads().refresh(slotsToRefresh);
            viewedSlots = [];

            //rotate ads after 15 seconds of being idle
            if (runSlots.adRefresh) {
                runSlots._adRefreshTimeout = window.setTimeout(runSlots.timedRefreshSlots, adRefreshDelay * 1000);
            }
        });
    };

    runSlots.timedRefreshSlots = function(){
        if (!runSlots.adRefresh) { return; }

        if (viewedSlots.length == 0) {
            // no viewed ads, don't bid yet -- wait another cycle
            runSlots._adRefreshTimeout = window.setTimeout(runSlots.timedRefreshSlots, adRefreshDelay * 1000);
            return;
        }

        _info('Bidding invoked by timer.');
        runSlots._shouldTimedRefresh = true; // this lets display logic know that we want to only cycle the viewed ads
        runSlots.pbjsRequestBids(function() {
            pbjs.setTargetingForGPTAsync();
            runSlots.rotate();
        });
    };

    runSlots.filterViewedSlots = function(slots) {
        return slots.filter(function(slot){
            return viewedSlots.indexOf(slot.i_name) != -1
        });
    };

    runSlots.setBidderTargeting = function() {
        pbjs.setTargetingForGPTAsync();
        try{
            if (_retargetAmazon) {
                amznads.getAdsCallback('3079', function() {
                    amznads.setTargetingForGPTAsync('amznslots');
                });
                googletag.pubads().clearTargeting('amznslots');
            } else {
                _retargetAmazon = true;
            }
        } catch(e){}
    };

    runSlots.setTargeting = function() {
        var safeFlags = runSlots.item.adConfig.safeFlags;
        var galleryState = safeFlags.indexOf('gallery') > -1 ? 'gallery' : 'nongallery';

        // determine the reason the item is allowed to show ads, for reporting purposes only
        var safeReason = null;
        if (galleryState == 'gallery') {
            if (safeFlags.indexOf('onsfw_mod_safe') > -1) {
                safeReason = 'g_u10_onsfw';
            } else if (safeFlags.indexOf('sixth_mod_safe') > -1) {
                safeReason = 'g_6th'
            } else {
                safeReason = 'g_o10';
            }
        } else {
            if (safeFlags.indexOf('sixth_mod_safe') > -1) {
                safeReason = 'ng_6th';
            } else if (safeFlags.indexOf('onsfw_mod_safe') > -1) {
                safeReason = 'ng_onsfw';
            } else {
                safeReason = 'ng_unknown';
            }
        }

        runSlots.apply_targeting_to_active_slots('url', location.href);
        runSlots.apply_targeting_to_active_slots('page', galleryState);
        runSlots.apply_targeting_to_active_slots('safe_reason', safeReason);
        runSlots.item.galleryTags && runSlots.item.galleryTags.forEach(function(tagObject, i) {
            runSlots.apply_targeting_to_active_slots('item_tag_'+i, rawUtils.dive(tagObject.tag));
        })
    };

    runSlots.blocked = function() {
        return _ads_blocked;
    };

    runSlots.init();
})();
