/*
* A set of raw util functions for use when main site code has not yet been loaded.
* Please keep this minimal.
*/

var rawUtils = {
    /**
    * Dive for a namespace path on an object without try/catch or typeof hell.
    * Example: dive(window, 'document.location.href')
    * Cribbed from Imgur.Util, but we load before Util.
    */
    dive: function (obj, path) {
        var sect = (''+path).replace(/^\./, '').split('.'),
            curr = sect.length > 0? sect.shift(): null;

        if(curr && obj && 'undefined' !== typeof obj[curr]) {
            if(sect.length === 0) {
                return obj[curr];
            } else {
                return rawUtils.dive(obj[curr], sect.join('.'));
            }
        }
    },

    /**
    * Get an array containing the keys/indexes/properties of a given object.
    */
    get_keys: function (obj) {
        if(typeof obj === 'object') {
            return Object.keys(obj);
        }
    },

    /**
    * Get the scroll position of the current page
    */
    get_scroll_position: function () {
        return  {
            top: (window.pageYOffset !== undefined) ? window.pageYOffset : (document.documentElement || document.body.parentNode || document.body).scrollTop,
            left: (window.pageXOffset !== undefined) ? window.pageXOffset : (document.documentElement || document.body.parentNode || document.body).scrollLeft
        }
    },

    /**
    * Get an array containing the values of a given object.
    */
    get_values: function (obj) {
        if(typeof obj === 'object') {
            return Object.keys(obj).map(function (key) { return obj[key] });
        }
    },

    /**
    * Check if a given value is an array and optionally check a minimum length.
    * If someOrCount is boolean true, then if the array is empty the check will fail.
    * If someOrCount is a number greater than -1, then it will be tested as a minimum length for the array.
    * Examples:
    *    rawUtils.is_array([]); // true
    *    rawUtils.is_array([], true); // false
    *    rawUtils.is_array([], 2); // false
    *    rawUtils.is_array(['a'], 2); // false
    *    rawUtils.is_array(['a', 'b'], 2); // true
    *    rawUtils.is_array(['a', 'b', 'c'], 2); // true
    */
    is_array: function(arr, someOrCount) {
        if(this.is_obj(arr) && Object.prototype.toString.call(arr) === '[object Array]') {
            if (typeof someOrCount === 'boolean') {
                return !someOrCount || (arr.length > 0);
            } else if(!isNaN(someOrCount) && (someOrCount > -1)) {
                return arr.length >= parseInt(someOrCount, 10);
            }

            return true;
        }

        return false;
    },

    /**
    * Check that a given value is an object and optionally check that it's not empty.
    */
    is_obj: function (thing, notEmpty) {
        if(typeof thing === 'object' && thing !== null) {
            if(notEmpty) {
                for(var subThing in thing) {
                    if(thing.hasOwnProperty(subThing)) {
                        return true;
                    }
                }

                return false;
            }

            return true;
        }

        return false;
    },

    /**
    * Merge multiple objects into one larget object.
    * Note that indexed arrays won't work well with this as the indexed keys will clash.
    */
    merge: function() {
        var iter;
        var key;
        var ret = {};

        for(iter = 0; iter < arguments.length; iter++) {
            if(!rawUtils.is_obj(arguments[iter], true)) {
                continue;
            }

            for(key in arguments[iter]) {
                if(!arguments[iter].hasOwnProperty(key)) {
                    continue;
                }

                ret[key] = arguments[iter][key];
            }
        }

        return ret;
    }
};
